.button{
  border: 0.3vmin solid #FFFFFF;
  border-radius: 2vmin;
  font-size: calc(1.7vmin);
  color: #FFFFFF;
  height: 7vmin;
  width: 15vmin;
  background-color: #43a7dd;
  cursor: pointer;
}

.button:hover {
  background-color: #43a7ff;
}

.space{
    width:5vmin;
    display:inline-block;
}

.logo {
  height: 6vmin;
  pointer-events: none;
}

.homebutton {
  border: 0.3vmin solid #FFFFFF;
  border-radius: 2vmin;
  font-size: calc(1.7vmin);
  color: #FFFFFF;
  height: 6vmin;
  width: 10vmin;
  background-color: #43a7dd;
  cursor: pointer;
}

.homebutton:hover {
  background-color: #43a7ff;
}

.page-header {
  background-color: #43a7dd;
  min-height: 8vh;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  color: white;
}

.table {
  border-collapse: collapse;
  width: 70%;
}

.table td, .table th {
  border: 0.3vmin solid #ddd;
}

.table tr:nth-child(even){
  background-color: #f2f2f2;
}

.table tr:hover {
  background-color: #ddd;
}

.table th {
  padding-top: 1vmin;
  padding-bottom: 1vmin;
  background-color: #43a7dd;
  color: white;
}

.panel {
  align-items: center;
  display: flex;
  text-align: center;
  justify-content: center;
}